import * as React from "react";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";
import FormAddon from "./FormAddon";
import FormItem from "./FormItem";

export interface FormProps extends React.FormHTMLAttributes<HTMLFormElement>, NamespaceWrapperProps {
  className ?: string;

  horizontal ?: boolean;
}

export default class Form extends NamespaceWrapper<FormProps, {}> {
  public static displayName = "Form";

  public render() {
    const {namespace, className, horizontal, ...props} = this.props;
    const classes = this.classes("Form", horizontal && "Form--horizontal", className);

    return <form className={classes} {...props} />;
  }
}

export class FormActions extends NamespaceWrapper<React.FormHTMLAttributes<HTMLDivElement> & NamespaceWrapperProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <div {...props}
                className={this.classes("Form__actions", className)} />;
  }
}

export {
  FormAddon,
  FormItem
};
